@use 'base/variables' as v;
@use 'base/mixins' as m;

/* HEADER SECTION */
.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: v.$spacing 0;

    @include m.desktop {
        flex-direction: row;
        justify-content: space-between;
    }

    &__logo {
        width: 30rem;
        margin-bottom: v.$spacing;

        @include m.desktop {
            margin-bottom: 0;
        }
    }
}