body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

p {
  color: #000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

.container {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #e8505b;
}

/* MEDIA QUERIES */
/* GRID */
/* BUTTON MIXIN STYLES */
/* HEADER SECTION */
.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 5rem 0;
}
@media (min-width: 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
  }
}
.header__logo {
  width: 30rem;
  margin-bottom: 5rem;
}
@media (min-width: 1024px) {
  .header__logo {
    margin-bottom: 0;
  }
}

.navbar {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  gap: 2rem;
}
@media (min-width: 1024px) {
  .navbar {
    flex-direction: row;
    width: auto;
  }
}
.navbar__link {
  color: #000;
  font-size: 2rem;
  font-weight: 700;
}
.navbar__link--register {
  background-color: #e8505b;
  font-size: 2rem;
  padding: 1.2rem 2.5rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  border: none;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
  transition: background-color 0.3s ease;
  -webkit-transition: background-color 0.3s ease;
  -moz-transition: background-color 0.3s ease;
  -ms-transition: background-color 0.3s ease;
  -o-transition: background-color 0.3s ease;
}
.navbar__link--register:hover {
  cursor: pointer;
  background-color: #e22331;
}
@media (min-width: 768px) {
  .navbar__link--register {
    width: auto;
  }
}

/* FORM SECTION */
.form {
  margin-top: 1.5rem;
}
@media (min-width: 768px) {
  .form__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.form__content {
  padding: 5rem;
  background-color: #e8505b;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}
.form__heading {
  color: #FFFFFF;
  text-align: center;
}
.form__field {
  margin-bottom: 2rem;
}
.form__label {
  color: #FFFFFF;
  text-transform: uppercase;
  display: block;
  margin-bottom: 0.5rem;
}
.form__input {
  width: 100%;
  padding: 1.5rem;
  border: none;
}
.form__submit {
  margin-top: 1rem;
  background-color: #455a64;
  font-size: 2rem;
  padding: 1.2rem 2.5rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  border: none;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
  transition: background-color 0.3s ease;
  -webkit-transition: background-color 0.3s ease;
  -moz-transition: background-color 0.3s ease;
  -ms-transition: background-color 0.3s ease;
  -o-transition: background-color 0.3s ease;
}
.form__submit:hover {
  cursor: pointer;
  background-color: #303f46;
}
@media (min-width: 768px) {
  .form__submit {
    width: auto;
  }
}
@media (min-width: 768px) {
  .form__submit {
    width: 100%;
  }
}
.form__image {
  width: 30rem;
  margin: 5rem auto 0;
}
@media (min-width: 768px) {
  .form__image {
    width: 100%;
  }
}

/* STEPS SECTION */
.steps {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .steps {
    margin-top: 5rem;
  }
}
.steps__wave {
  margin-bottom: -0.1rem;
}
.steps__content {
  background-color: #e8505b;
}
@media (min-width: 768px) {
  .steps__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
  }
}
.steps__heading {
  color: #FFFFFF;
  text-align: center;
}
.steps__heading--description {
  display: block;
  font-size: 2rem;
  margin-top: 1rem;
}
.steps__card {
  transition: transform 0.3s ease;
  -webkit-transition: transform 0.3s ease;
  -moz-transition: transform 0.3s ease;
  -ms-transition: transform 0.3s ease;
  -o-transition: transform 0.3s ease;
}
.steps__card:hover {
  transform: rotate(-5deg) scale(1.2);
  -webkit-transform: rotate(-5deg) scale(1.2);
  -moz-transform: rotate(-5deg) scale(1.2);
  -ms-transform: rotate(-5deg) scale(1.2);
  -o-transform: rotate(-5deg) scale(1.2);
}

.testimonials__heading {
  text-align: center;
  margin-top: 5rem;
}
.testimonials__content {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
@media (min-width: 768px) {
  .testimonials__content {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1.2rem;
  }
}

.testimonial {
  background-color: #455a64;
  padding: 3rem;
  border-radius: 1rem;
  -webkit-border-radius: 1rem;
  -moz-border-radius: 1rem;
  -ms-border-radius: 1rem;
  -o-border-radius: 1rem;
}
@media (min-width: 768px) {
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(2) {
    grid-column: 4/6;
  }
  .testimonial:nth-child(3) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(4) {
    grid-row: 3/4;
    grid-column: 1/6;
  }
  .testimonial:nth-child(5) {
    grid-column: 3/6;
  }
}
@media (min-width: 1024px) {
  .testimonial:nth-child(1) {
    grid-column: 1/4;
  }
  .testimonial:nth-child(2) {
    grid-column: 1/3;
  }
  .testimonial:nth-child(3) {
    grid-column: 3/5;
  }
  .testimonial:nth-child(4) {
    grid-row: 1/3;
    grid-column: 5/6;
  }
  .testimonial:nth-child(5) {
    grid-row: 1/2;
    grid-column: 4/5;
  }
}
.testimonial__header {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.testimonial__autor {
  color: #FFFFFF;
  font-weight: 700;
  margin-bottom: 0.7rem;
}
.testimonial__image {
  width: 8rem;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.testimonial__quote {
  color: #FFFFFF;
  margin-top: 2rem;
  padding-left: 2rem;
  font-size: 1.8rem;
  line-height: 1.5;
  position: relative;
}
.testimonial__quote::before {
  content: ' " ';
  opacity: 0.2;
  font-size: 8rem;
  position: absolute;
  top: -3.5rem;
  left: 0;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}